.LibraryPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--bkg-color);
    min-height: 100vh;
}

.LibraryPage-content {
    margin-top: 15vh;
    width: 80%;
    padding: 20px;
}

.LibraryPage-title {
    font-size: 2.5rem;
    color: var(--text-color);
    margin-bottom: 20px;
}

.LibraryPage-lists {
    display: flex;
    justify-content: space-between;
}

.LibraryPage-list {
    width: 48%;
    background-color: var(--pastel-light-color);
    border-radius: 10px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin: 20px;
}

.LibraryPage-listTitle {
    font-size: 2rem;
    color: var(--bkg-color);
    margin-bottom: 20px;
}